import { Component, OnInit } from '@angular/core';
import { Park, ParkList } from '../np.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-np-content',
  templateUrl: './np-content.component.html',
  styleUrls: ['./np-content.component.css']
})
export class NpContentComponent implements OnInit {
  parkUrl: string;
  park: Park;
  parkList: ParkList;

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.parkUrl = this.route.snapshot.params['name'];
    let parkList = new ParkList();
    this.park = parkList.getPark(this.parkUrl);
  }

}
