import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { NpGridComponent } from './np/np-grid/np-grid.component';
import { NpComponent } from './np/np.component';
import { NpMainComponent } from './np/np-main/np-main.component';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { NpTableComponent } from './np/np-table/np-table.component';
import { NpContentComponent } from './np/np-content/np-content.component';
import { AboutComponent } from './about/about.component';
import { PgComponent } from './pg/pg.component';

const appRoutes: Routes = [
  {'path': '', component: HomeComponent},
  {'path': 'us-national-parks', component: NpComponent},
  {'path': 'us-national-parks/:name', component: NpMainComponent},
  {'path': 'photography', component: PgComponent},
  {'path': 'about', component: AboutComponent},
  {'path': 'not-found', component: NotFoundComponent},
  {'path': '**', redirectTo: '/not-found'},
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    NpGridComponent,
    NpComponent,
    NpMainComponent,
    HomeComponent,
    NotFoundComponent,
    NpTableComponent,
    NpContentComponent,
    AboutComponent,
    PgComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot(appRoutes),
    BrowserAnimationsModule,
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
