import { Component, OnInit } from '@angular/core';
import * as usStates from '../../assets/data/us-states.json';
import * as usParks from '../../assets/data/us-parks.json';

@Component({
  selector: 'app-np',
  templateUrl: './np.component.html',
  styleUrls: ['./np.component.css']
})
export class NpComponent implements OnInit {
  parkList = new ParkList();
  menuStyle = MenuStyle;
  menuSort = MenuSort;
  style: MenuStyle;
  sort: MenuSort;

  constructor() {}

  ngOnInit() {
    this.style = this.menuStyle.GRID;
    this.sort = this.menuSort.VISITED;
  }

  changeMenuStyle(clickedStyle: MenuStyle) {
    if (clickedStyle == this.menuStyle.TABLE) {
      this.style = this.menuStyle.TABLE;
    } else {
      this.style = this.menuStyle.GRID;
    }
  }

  changeMenuSort(clickedSort: MenuSort) {
    if (clickedSort == this.menuSort.VISITED) {
      this.sort = this.menuSort.VISITED;
    } else if (clickedSort == this.menuSort.STATE) {
      this.sort = this.menuSort.STATE;
    } else {
      this.sort = this.menuSort.NAME;
    }
  }

}

export enum MenuStyle {
  GRID,
  TABLE,
}

export enum MenuSort {
  VISITED,
  NAME,
  STATE,
}

export class Park {
  parkUrl: string;
  ch:      string;
  en:      string;
  states:  string[];
  npsUrl:  string;
  region:  string;
  visited: boolean;

  stateList: States;
  
  constructor(
      parkUrl: string = '',
      ch: string = '',
      en: string = '',
      states: string[] = [],
      npsUrl: string = '',
      region: string = '',
      visited: boolean = false) {
    this.parkUrl = parkUrl;
    this.ch = ch;
    this.en = en;
    this.states = states;
    this.npsUrl = npsUrl;
    this.region = region;
    this.visited = visited;
    this.stateList = new States();
  }

  getParkUrl() {
    return this.parkUrl;
  }

  getNameShortCh() {
    return this.ch;
  }

  getNameCh() {
    return this.ch+ '國家公園';;
  }

  getNameShortEn() {
    return this.en;
  }

  getNameEn() {
    if (this.parkUrl == 'american-samoa') {
      return ' National Park of ' + this.en;
    } else {
      return this.en + ' National Park';
    }
  }

  getStateNumberArray(): number[] {
    return Array.from({length: this.states.length}, (v, i) => i);
  }

  getStateAbbre(): string[] {
    return this.states;
  }

  getStateCh(): string[] {
    return this.states.map(state => {
      if (state == 'VI' || state == 'AS') {
        return this.stateList.getNameCh(state);
      } else {
        return this.stateList.getNameCh(state) + '州';
      }
    });
  }

  getStateEn(): string[] {
    return this.states.map(state => this.stateList.getNameEn(state));
  }

  getNpsUrl() {
    return `https://www.nps.gov/${this.npsUrl}/index.htm`;
  }

  getVisited() {
    return this.visited;
  }

  getThumbnail() {
    if (this.visited) {
      return `/assets/parks/${this.parkUrl}/thumbnail.jpg`
    } else {
      return "https://media.istockphoto.com/vectors/vector-realistic-isolated-404-not-found-error-lettering-with-glitch-vector-id990584628"
    }
  }
}

export class ParkList {
  usParks: Map<string, Park>;

  constructor() {
    this.usParks = new Map<string, Park>();
    let parks = usParks['default'];
    for (var park in parks) {
      let parkDetails = new Park(
        park,
        parks[park]['ch'],
        parks[park]['en'],
        parks[park]['states'],
        parks[park]['nps_url'],
        parks[park]['region'],
        parks[park]['visited']
      );
      this.usParks.set(park, parkDetails);
    }
  }

  getLength() {
    return this.usParks.size;
  }

  getPark(park: string) {
    return this.usParks.get(park);
  }

  getParks(sort: MenuSort) {
    function byVisited(a: Park, b: Park) {
      if (
          (a.getVisited() && b.getVisited()) ||
          (!a.getVisited() && !b.getVisited())) {
        return a.getParkUrl() < b.getParkUrl() ? -1 : 1
      } else {
        return a.getVisited() ? -1 : 1
      }
    }
    function byEnName(a: Park, b: Park) {
      return a.getParkUrl() < b.getParkUrl() ? -1 : 1
    }
    function byState(a: Park, b: Park) {
      return a.getStateAbbre()[0] < b.getStateAbbre()[0] ? -1 : 1
    }

    if (sort == MenuSort.VISITED) {
      return Array.from( this.usParks.values() ).sort(byVisited);
    } else if (sort == MenuSort.STATE) {
      return Array.from( this.usParks.values() ).sort(byState);
    } else {
      return Array.from( this.usParks.values() ).sort(byEnName);
    }
  }
}

interface StateDetails {
  abbre: string;
  ch: string;
  en: string;
}

class States {
  usStates: Map<string, StateDetails>;

  constructor() {
    this.usStates = new Map<string, StateDetails>();
    let states = usStates['default'];
    for (var state in states) {
      let stateDetails: StateDetails = { 
        abbre: state,
        ch: states[state]['ch'],
        en: states[state]['en']
      };
      this.usStates.set(state, stateDetails);
    }
  }

  getAbbre(name: string) {
    return this.usStates.get(name).abbre;
  }

  getNameCh(name: string) {
    return this.usStates.get(name).ch;
  }

  getNameEn(name: string) {
    return this.usStates.get(name).en;
  }
}
