import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ParkList, Park } from '../np.component'


@Component({
  selector: 'app-np-main',
  templateUrl: './np-main.component.html',
  styleUrls: ['./np-main.component.css']
})
export class NpMainComponent implements OnInit {
  parkUrl: string;
  park: Park;
  stateNames: string[][];

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.parkUrl = this.route.snapshot.params['name'];
    let parkList = new ParkList();
    this.park = parkList.getPark(this.parkUrl);
  }
}
