import { Component, OnInit, Input } from '@angular/core';
import { Park } from '../np.component';
import {
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/animations';

@Component({
  selector: 'app-np-grid',
  templateUrl: './np-grid.component.html',
  styleUrls: ['./np-grid.component.css'],
  animations: [
    trigger('dimZoom', [
      state('normal', style({
        transform: 'scale(1)',
        filter: 'brightness(100%)'
      })),
      state('zoomed', style({
        transform: 'scale(1.2)',
        filter: 'brightness(50%)'
      })),
      transition('normal <=> zoomed', animate(250))
    ]),
    trigger('shown', [
      state('normal', style({
        opacity: 0
      })),
      state('zoomed', style({
        opacity: 1
      })),
      transition('normal <=> zoomed', animate(250))
    ])
  ]
})
export class NpGridComponent implements OnInit {
  @Input() park: Park;
  imgState: string;
  btnState: string;

  constructor() {}

  ngOnInit() {
    this.imgState = 'normal';
    this.btnState = 'normal';
  }

  mouseEnter() {
    this.imgState = 'zoomed';
    this.btnState = 'zoomed';
  }

  mouseLeave() {
    this.imgState = 'normal';
    this.btnState = 'normal';
  }

}
